import React from 'react';
import logo from './logo.svg';
import { Layout, Row, Col, Input, Select, Radio, Button, Affix, message, notification } from 'antd';
import {DefaultUrl, TvUrl} from './const'
import './Vedio.less';
const { Header, Footer, Content } = Layout;
const { Option } = Select;
const { Search } = Input;
class Vedio extends React.Component {
  constructor (props) {
    super(props)
    this.state = {
      showVedio: false,
      defaultUrlPre: DefaultUrl['one'],
      vedioUrl: '',
      defaultUrlFlag: true,
      defaultUrl: DefaultUrl['default1'],
      showCode: false,
      showCodeAgain: true
    }
  }
  
  componentWillMount () {
  }
  componentWillReceiveProps (next) {

  }
  handleChange = (value) => {
    const flag = this.state.defaultUrlFlag
    const defaultUrlFlag = this.state.defaultUrlFlag
    this.setState({
      defaultUrlPre: DefaultUrl[value],
      defaultUrl: flag ? DefaultUrl['default1'] : DefaultUrl['default2'],
      defaultUrlFlag: !defaultUrlFlag
    })
  }
  handleSearch = (val) => {
    if(!val || val.indexOf('.com') === -1) {
      message.error('视频播放链接非法！')
      return
    }
    const vedioUrl = this.state.defaultUrlPre + val
    this.setState({
      showVedio: true,
      vedioUrl: vedioUrl,
      showCodeAgain: true
    })
    setTimeout(()=>{
      this.setState({
        showCode: true
      })
    },50000)
  }

  handleClick = (e) => {
    const tv = e.target.value
    window.open(TvUrl[tv])
  }

  handleShowCode = (e) => {
    const showCode = e
    this.setState({
      showCode: false
    })
    if('later' === showCode && this.state.showCodeAgain) {
      this.handleShowCodeLater()
    }
  }

  handleShowCodeLater = () => {
    setTimeout(()=>{
      this.setState({
        showCode: true,
        showCodeAgain: false
      })
    },50000)
  }

  resetHandle = () => {
    this.refs.searchBar.input.state.value='';
    this.refs.searchBar.input.input.value='';
  }

  onMessage = () => {
    notification['info']({
      message: '使用方法',
      duration: 8,
      description:
        <div>
          <p>1.打开视频播放网站(爱奇艺，优酷等等)</p>
          <p>2.找到VIP视频，点击播放后复制链接</p>
          <p>3.将链接粘贴到搜索框中，点击搜索即可观看</p>
          <p>4.播放失败时切换播放源即可</p>
          <p>5.觉得好用请打赏，虽然打赏并不会提升观赏品质</p>
        </div>
    })
  }

  render () {
    const { showVedio, vedioUrl, showCode, showCodeAgain } = this.state
    return (
      <div>
        <Layout className='App'>
          <Header className='App-header'>
            <Row>
              <Col span={4} className='App-logo'><span >V视频</span></Col>
              <Col span={1} offset={19}><Button type="dashed" size="large" ghost onClick={() => {this.onMessage.call(this)}}>使用方法</Button></Col>
            </Row>
          </Header>
          <Content className='App-content'>
            {!showVedio 
              ? <img src={logo} className='content-img' alt="logo" />
                : <iframe 
                    title='navigation'
                    style={{width:'100%', height:'80vh', overflow:'visible'}}
                    ref="iframe" 
                    src={vedioUrl} 
                    width="100%"
                    scrolling="no" 
                    frameBorder="0"
                />}
          </Content>
          <Footer>
            <div style={{display: 'inline-flex', height: '5vh' }}>
              <Select defaultValue="one" size="large" style={{ width: 120 }} onChange={(value)=>{this.handleChange.call(this, value)}}>
              <Option value="one">播放源①</Option>
              <Option value="two">播放源②</Option>
              <Option value="three">播放源③</Option>
              <Option value="four">播放源④</Option>
              <Option value="five">播放源⑤</Option>
              </Select>
              <Search
                ref= "searchBar"
                placeholder="视频网站播放链接"
                enterButton="搜索"
                size="large"
                onSearch={value => {this.handleSearch.call(this, value)}}
                allowClear
                style={{ width: '80vh' }}
              />
              <Button size="large" onClick={() => {this.resetHandle.call(this)}}>清空</Button>
              <Radio.Group size="large" onChange={(value) => {this.handleClick.call(this, value)}}>
                <Radio.Button value="iqiyi">爱奇艺</Radio.Button>
                <Radio.Button value="tenxun">腾讯</Radio.Button>
                <Radio.Button value="youku">优酷</Radio.Button>
                <Radio.Button value="mangguo">芒果</Radio.Button>
                <Radio.Button value="souhu">搜狐</Radio.Button>
              </Radio.Group>
            </div>
          </Footer>
        </Layout>
        {
          showCode
          ? <Affix className='Affix' style={{ position: 'absolute', bottom: 220, right: 120 }} offsetTop={120}>
              <img style={{width: 240, height: 240}} src={'./zscode.jpg'} alt='赞赏'/>
              <br />
              {showCodeAgain 
                ? <Radio.Group onChange={(value)=>{this.handleShowCode.call(this, value.target.value)}}>
                    <Radio.Button value="no">不再出现</Radio.Button>
                    <Radio.Button value="later">一会再说</Radio.Button>
                  </Radio.Group>
                  : <Button className='Affix-btn' onClick={()=>{this.handleShowCode.call(this, 'no')}}>不再出现</Button>}
            </Affix>
            : null
        }
      </div>
    )
  }
}
export default (Vedio)
